export default {
  name: 'DefaultLayout',
  data () {
    return {
      top: 0
    }
  },
  mounted () {
    const self = this
    document.addEventListener('scroll', function () {
      const st = window.pageYOffset || document.documentElement.scrollTop
      self.top = st <= 0 ? 0 : st
    }, false)
  }
}
