import Loader from '@/components/Loader'

export default {
  name: 'FooterComponent',
  components: {
    Loader
  },
  data () {
    return {
      email: '',
      loading: false,
      solutionLinks: [
        {
          name: 'About Us',
          to: '/about'
        },
        {
          name: 'For Teams',
          to: '/solutions#teams'
        },
        {
          name: 'For Businesses',
          to: '/solutions#business'
        },
        {
          name: 'For Individuals',
          to: '/solutions#individual'
        }
      ],
      supportLinks: [
        {
          name: 'Blog',
          url: 'https://gettonote.medium.com/'
        },
        {
          name: 'FAQs',
          to: '/faq'
        },
        {
          name: 'Careers',
          to: '/careers'
        },
        {
          name: 'Contact Us',
          to: '/contact-sales'
        }
      ],
      legalLinks: [
        {
          name: 'Privacy Policy',
          to: '/privacy-policy'
        },
        {
          name: 'Terms of Use',
          to: '/terms_conditions'
        },
        {
          name: 'Cookies Policy',
          to: '/cookies'
        }
      ],
      socialLinks: [
        {
          name: 'Facebook',
          url: 'https://web.facebook.com/GetToNote'
        },
        {
          name: 'Twitter',
          url: 'https://twitter.com/GetToNote'
        },
        {
          name: 'Instagram',
          url: 'https://www.instagram.com/mytonote/'
        },
        {
          name: 'LinkedIn',
          url: 'https://www.linkedin.com/company/tonote/'
        }
      ],
      buttonText: 'Join Our Community',
      isHidden: false,
      errorMsg: ''
    }
  },
  methods: {
    async sendEmail () {
      this.errorMsg = ''
      const self = this
      this.loading = true
      try {
        const { status, code } = await this.$axios.$post('api/v1/user/profile/join-community', { email: this.email })
        if (status === 'success' && code === 200) {
          this.email = ''
          this.buttonText = 'Message Sent'
          this.isHidden = true
        }
      } catch (error) {
        this.errorMsg = 'Unable to process request.'
        return error
      } finally {
        this.loading = false
        setTimeout(() => {
          self.buttonText = 'Join Our Community'
          this.isHidden = false
        }, 3000)
      }
    }
  }
}
